<template>
   <div id="guide_img" class="apiModule">
       <h2 class="cy_h2"><strong>IMAGE</strong>图片规范</h2>
        <ul class="guide_con">
            <li>
                <h3>内容图片</h3>
                <p>
                    网站中使用到的图片，通常为四种文件格式： jpg、png(包括png24、png8)、gif、webp。<br/>
                    图像质量要求和图像文件大小决定你用什么格式的图片，用较小的图片文件呈现较好的图像质量。
                </p>
                <ol>
                    <li>当图片色彩过于丰富且无透明要求时，建议采用jpg格式并保存为较高质量。</li>
                    <li>当图片色彩过于丰富又有透明或半透明要求或阴影效果时，建议采用png24格式，并对IE6进行png8退化（或在不得已情况下使用滤镜）。</li>
                    <li>当图片色彩不太丰富时无论有无透明要求，请采用png8格式，大多数情况下建议采用这种格式。</li>
                    <li>当图片有动画时，只能使用gif格式。gif动画文件需注意控制图片体积。</li>
                </ol>
                <p>
                    你可以使用工具对图片进行再次压缩，但前提是不会影响色彩和透明。<br/>
                    此外，可以遵循此规则： 最终k数原则上不能超过以下公式计算出的结果：k数≤宽*高*0.4/1024		
                </p>
            </li>
            <li>
                <h3>背景图片</h3>
                <p>
                    建议背景图片以bg_开头来命名，图标以ico_开头来命名，按钮则以btn_开头来命名，雪碧图则以sprite_开头来命名。<br/>
                    背景图片所在的目录建议和CSS文件放在相同目录下，以方便CSS中定义使用（如url(images/btn.png)），以及以后可能的文件迁移。
                </p>
                <ol>
                    <li>
                        <p>运用csssprite技术将小的背景图或图标合并到一个文件，减小页面http请求。在对应的sprite psd源图中划参考线，并保存至img目录下。</p>
                        <p>
                            合并的时候，需要注意如下事项：<br/>
                            1)、单个图标之间必须保留空隙，空隙大小由容器大小及显示方式决定。这样做的好处是既考虑了“容错性”又提高了图片的可维护性。<br/>
                            2)、图标的排列方式，也由容器大小及显示方式决定。排列方式分为以下几种：横向排列（容器宽度有限）、纵向排列（容器高度有限）、斜线排列（容器宽高不限），靠左排列（容器背景居左）、靠右排列（容器背景居右）、水平居中排列（容器背景水平居中）、垂直居中排列（容器背景垂直居中）。<br/>
                            3)、合并后图片大小不宜超过50K，建议大小在20K-50K之间。
                        </p>
                        <p>
                            当然，并不是把所有的图标都合并在一张图片里就是最好的，除了要控制图片大小之外还要注意以下方法：<br/>
                            1)、按照图片排列方式，把排列方式一样的图片进行合并，便于样式控制。<br/>
                            2)、按照模块或元件，把同属于一个模块或元件的图片进行合并，方便模块或元件的维护。<br/>
                            3)、按照图片大小，把大小一致或差不多的图片进行合并，可充分利用图片空间。<br/>
                            4)、按照图片色彩，把色彩一致或差不多的图片进行合并，保证合并后图片的色彩不过于丰富，可防止色彩失真。
                        </p>
                    </li>
                    <li>
                        有固定宽高之一的平铺图片根据轴向以bg_x.或bg_y命名整合在一起，图片宽度（横向平铺）或高度（竖向平铺）以10px以上为宜（减少平铺渲染消耗），间隔应考虑背景延展性，存储成质量较高的的jpg或者png 8。<br/>
                        考虑到平铺图片的特性，若有尺寸上的修改，原则上以删除旧图，在图片末尾处添加新图为准，不要试图在原处修改，以免出现管理混乱。
                    </li>	
                    <li>X、Y轴同时有平铺需求的图片独立命名。</li>
                </ol>
            </li>
            <li>
                <h3>性能优化</h3>
                <ol>
                    <li>
                        <h4>模块/图片 懒加载；</h4>
                        <p>只加载用户视觉范围内的模块和图片；其他模块在用户需要时加载；</p>
                    </li>
                    <li>
                        <h4>减少图片请求</h4>
                        <p>尽量减少图片请求次数，背景做成雪碧图；首屏次屏图分开；图片大小根据主流网络与请求资源来决定；影响视觉的背景图可提前在页面加载。</p>
                    </li>
                    <li>
                        <h4>减少图片体积与尺寸;</h4>
                        <p>在不失真的前提下体积减少到最小；尽量图片尺寸与分辨率成1:1关系。</p>
                    </li>
                </ol>
            </li>
        </ul>
  </div>
</template>

<script> 
  export default {
    name: 'guideimg',
    data() {
      return {}
    },
    mounted() {},
    methods: {}
  }
</script>
<style scoped>
 @import './index.css';
</style>
 



