<template>
  <div class="develop">
    <cynav currIndex="0"></cynav>
    <div class="cy_menu" ref="cy_menu">
        <div class="cy_menu_con">
            <dl class="menu" ref="menu">
                <dt>开发规范</dt>
                <dd class="curr"><a href="javascript:void(0)">1-总的规范</a></dd>
                <dd><a href="javascript:void(0)">2-文件规范</a></dd>
                <dd><a href="javascript:void(0)">3-HTML规范</a></dd>
                <dd><a href="javascript:void(0)">4-CSS规范</a></dd>
                <dd><a href="javascript:void(0)">5-JS规范</a></dd>
                <dd><a href="javascript:void(0)">6-图片规范</a></dd>
                <dd><a href="javascript:void(0)">7-延伸内容</a></dd>
            </dl>         
        </div>
    </div>
    <div class="cy_center" ref="menucon">
        <guidetag></guidetag>
        <guideall></guideall>
        <guidefile></guidefile>
        <guidehtml></guidehtml>
        <guidecss></guidecss>
        <guidejs></guidejs>
        <guideimg></guideimg>
        <guideext></guideext>
    </div>    



  </div>
</template>

<script>
  import sliderScroll from 'cy_static/sliderScroll';  
  import floatNav from 'cy_static/floatNav';  
  
  import guidetag from "./guidetag"
  import guideall from "./guideall"
  import guidefile from "./guidefile"
  import guidehtml from "./guidehtml"
  import guidecss from "./guidecss"
  import guidejs from "./guidejs"
  import guideimg from "./guideimg"
  import guideext from "./guideext"

  export default {
    name: 'develop',
    data() {
      return {}
    },
    components:{
        guidetag,
        guideall,
        guidefile,
        guidehtml,
        guidecss,
        guidejs,
        guideimg,
        guideext
    },
    mounted() {
        let m = this;
        m.initOutWin();
        floatNav.anchor({
            menuItem : m.$refs.menu.querySelectorAll('dd'), 		 // 固定元素选择器多个','分隔
            elemItem : m.$refs.menucon.querySelectorAll('.apiModule'), // 内容元素选择器多个','分隔
            currCss : 'curr', 	// 选中固定元素样式
            speeds:[-90, -90,-90,-90,-90,-90,-90] 		// 内容元素修正偏移量
        });
    },
    methods: {          
      initOutWin : function () {
          var m = this, myMember = document.querySelector('.cy_menu');   
          if (m.outWin) return;             
          m.outWin = sliderScroll({
              elem: myMember,// 窗口元素
              content: ".cy_menu_con",
              isTopEffect: false,
              isBotEffect: false,
              hasMask:true,
              hasHoveScrollbar: true,
              pushDown: {}// 底部加载
          });
          m.outWin.isEndPull(true);
          m.outWin.isEndPush(true);
      }
    },
    activated () {
        this.outWin.setActivated(true);
    },
    deactivated(){
        this.outWin.setActivated(false);
    },
    beforeDestroy() {
        this.outWin.destroy();
    }
  }
</script>
 

 



